* {
  box-sizing: border-box;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

/* Header */
.header {
  font-size: 2rem;
  padding: 1em;
  background-color: blanchedalmond;
}

.header-logo {
  color: green;
  margin-right: 0.5em;
}

.header-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  background-color: green;
  color: white;
  border-radius: 50%;
}

/* AddForm */
.add-form {
  display: flex;
  width: 500px;
  margin-left: 50px;
  margin-top: 20px;
}

.add-input {
  flex-grow: 9;
  font-size: 1.5rem;
}

.add-button {
  flex-grow: 1;
  height: 100%;
  font-size: 1.3rem;
  padding: 0.3em 0.5em;
  background-color: green;
  color: white;
}

/* Habit */
.habit {
  display: flex;
  align-items: center;
  font-size: 2.5rem;
  padding: 0.5rem;
  list-style: none;
  margin: 0 auto;
  margin-bottom: 10px;
  border-left: 5px solid yellowgreen;
  border-bottom: 3px solid #ccc;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-btns {
  margin-left: auto;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  cursor: pointer;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}

.habits {
  width: 500px;
  margin-left: 50px;
  padding: 0;
}

.habits-reset {
  display: flex;
  margin-left: 50px;
  width: 500px;
}

.habits-resetBtn {
  margin-left: auto;
  font-size: 1.5rem;
  padding: 0.2em 1em;
  background-color: green;
  color: white;
}
